import {React, useEffect, useState} from "react";
import Img from "react-cool-img";
import './contributorsComponent.css';
const placeHolder = '../../placeholder/default-placeholder.png'

function ContributorsComponent() {
    const [logo, setLogo] = useState('');
    const url = 'https://portfolio-back-heroku.herokuapp.com/api/logo/getContributorsLogo'
    const getContributors = async () => {
        const data = await fetch(url);
        const response = await data.json();
        setLogo(response.contributorslogo)
    };
    useEffect(() => {
        getContributors()
    })
    return (
        <section className="contributors-body">
            <div className="contributors-body--title-container text-white">
                <span>web design and logo by </span>
            </div>
            <Img 
            placeholder={placeHolder}
            src={logo}
            alt="logo"
            className="contributors--logo"
            />
        </section>
    );
}

export default ContributorsComponent;